import React from 'react';
import "./BudgetBuddy.css";

function BudgetBuddy() {
    return (
        <div className="budgetBuddy-container"
            /* Animation */  
            data-aos="fade-down"
            data-aos-delay="50"
            data-aos-duration="1000"
            data-aos-easing="ease-in-out"
            data-aos-mirror="true"
            data-aos-once="true" 
                 
        >
            <a className="back-home-button" href="/">X</a>
            <h1 className="budgetBuddy-title">BudgetBuddy</h1>
            <h4 className="budgetBuddy-motto">A web app that helps your budget</h4> 

            <div class="row"
                /* Animation */  
                data-aos="fade-up"
                data-aos-delay="50"
                data-aos-duration="1000"
                data-aos-easing="ease-in-out"
                data-aos-mirror="true"
                data-aos-once="true"           
            >
              <div class="column">
                <img src="/images/BudgetBuddy1.png" alt="Trulli" className="project-picture"></img>  
              </div>
              <div class="column">
              <img src="/images/BudgetBuddy2.png" alt="Trulli" className="project-picture"></img>   
              </div>
              <div class="column">
              <img src="/images/BudgetBuddy3.png" alt="Trulli" className="project-picture"></img>   
              </div>
            </div>
            <p className="project-description"
                /* Animation */  
                data-aos="fade-in"
                data-aos-delay="300"
                data-aos-duration="1000"
                data-aos-easing="ease-in"
                data-aos-mirror="true"
                data-aos-once="true"             
            >
                BudgetBuddy is a simple web application that allows users to set a budget and track daily income and expenses. With its easy-to-use interface, the user can keep track of 
                income and expenses as well as compare the different types of expenses with the provided interactive charts.
            </p> 

            <h2 className="techUsed-title">Tech Used:</h2> 
            <div class="tech-row">
              <div class="tech-column">
                <h3 className="tech-text">React</h3> 
              </div>
              <div class="tech-column">
                <h3 className="tech-text">HTML5</h3>   
              </div>
              <div class="tech-column">
                <h3 className="tech-text">CSS3</h3>    
              </div>
              <div class="tech-column">
                <h3 className="tech-text">JavaScript</h3>    
              </div>
              <div class="tech-column">
                <h3 className="tech-text">Reactstrap</h3>    
              </div>
              <div class="tech-column">
                <h3 className="tech-text">ChartJS</h3>    
              </div>
            </div>
          
        </div>
    )
}

export default BudgetBuddy;
